<template>
  <div class="content-area">
    <!-- 控制項區域 -->
    <div class="controls">
      <!-- 切換模式的按鈕與下拉選單 -->
      <div class="mode-switch">
        <button @click="currentMode = 'list'">列表模式</button>
        <button @click="currentMode = 'table'">表格模式</button>
      </div>
      <!-- 下拉選單 -->
      <select v-if="dropdowns.length > 0" v-model="selectedItem" @change="handleDropdownChange">
        <option v-for="option in dropdowns" :key="option.id" :value="option.value">
          {{ option.label }}
        </option>
      </select>
    </div>

    <!-- 根據模式顯示內容 -->
    <div class="mode-content">
      <div v-if="currentMode === 'list'" class="list-mode-container">
        <!-- ListMode 組件 -->
        <ListMode :items="items" />
      </div>
      <div v-else-if="currentMode === 'table'" class="table-mode-container">
        <!-- TableMode 組件 -->
        <TableMode :items="items" />
      </div>
    </div>

    <!-- AdvertisementBlock 組件 -->
    <AdvertisementBlock :advertisements="advertisements" :isAdFixed="isAdFixed" />
  </div>
</template>

<script>
import ListMode from './ListMode.vue';
import TableMode from './TableMode.vue';
import AdvertisementBlock from './Advertisement.vue';

export default {
  name: 'ContentArea',
  components: {
    ListMode,
    TableMode,
    AdvertisementBlock,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
    advertisements: {
      type: Array,
      default: () => [],
    },
    isAdFixed: {
      type: Boolean,
      default: false,
    },
    dropdowns: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      currentMode: 'list', // 默認列表模式
      selectedItem: null, // 選擇的下拉選項
    };
  },
  methods: {
    handleDropdownChange() {
      // 處理下拉選單選擇事件
      console.log('Selected item:', this.selectedItem);
    },
  },
};
</script>

<style scoped>
.content-area {
  display: flex;
  flex-wrap: wrap;
}

.controls {
  display: flex;
  justify-content: space-between; /* 將內容分散對齊 */
  width: 100%; /* 填滿父容器的寬度 */
  margin-bottom: 20px; /* 底部間距 */
}

.mode-switch {
  display: flex;
}

.mode-switch button {
  margin-right: 10px;
}

.mode-content {
  flex: 1; /* 調整模式內容區域的寬度，這裡假設佔比為1 */
}

.table-mode-container,
.list-mode-container {
  margin-bottom: 20px;
}
</style>
